import formEmployee from "./formEmployee";
import { POST, DELETE } from '../../api/index'
import { HalfCircleSpinner } from "epic-spinners";
import { success, deleteData, error } from "../../service/MessageBox"

export default {
  name: "client",
  components: { formEmployee, HalfCircleSpinner },
  data() {
    return {
      isLoading: false,
      isShow: false,
      pagination: {
        page: 1,
        rowsPerPage: 5
      },
      totalData: 0,
      disableView: false,
      disableEdit: false,
      textTitle: "",
      dataEmployee: [],
      form: {
        Id: null,
        Name: null,
        OfficePhone: null,
        HandPhone: null,
        Email: null,
        Divisi: null,
        JenisLayanan: null,
        PICExperd: null,
        ClientCompanyID: null,
      },
      headers: [{
        text: "Aksi",
        value: "aksi",
        sortable: false,
        align: "left",
        class: ""
      }, {
        text: "Employee No",
        value: "EmployeeNo",
        align: "center"
      }, {
        text: "Name",
        value: "Name",
        align: "center"
      }, {
        text: "Email",
        value: "Email",
        align: "center"
      }, {
        text: "Phone No",
        value: "PhoneNo",
        align: "center"
      }, {
        text: "Employee Type",
        value: "EmployeeType",
        align: "center"
      }, {
        text: "Is Partner",
        value: "IsPartner",
        align: "center"
      }, {
        text: "Initial",
        value: "Initial",
        align: "center"
      }]
    };
  },
  created() {
    this.loadData()
  },
  beforeMount: function () { },
  computed: {},
  methods: {
    toggleInputDialog: function () {
      this.isShow = !this.isShow;
      this.textTitle = "Tambah Employee"
    },
    onClose: function () {
      this.isShow = false;
      this.disableEdit = false;
      this.disableView = false;
      this.resetForm()
    },
    loadData: function () {
      try {
        this.isLoading = true
        const { page, rowsPerPage } = this.pagination;
        POST(`employee/employee_cr/${page}/${rowsPerPage}`).then((res) => {
          this.dataEmployee = res.Data.map(x => {
            return ({
              Id: x.Id,
              Name: x.Name,
              OfficePhone: x.OfficePhone,
              HandPhone: x.HandPhone,
              Email: x.Email,
              JenisLayanan: x.JenisLayanan,
              PICExperd: x.PICExperd,
              ClientCompanyID: x.ClientCompanyID,
            })
          })
          this.totalData = res.TotalData;
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onEdit: function (id) {
      try {
        this.isLoading = true
        POST('employee/employee_cr', { Id: id }).then((res) => {
          this.form = res[0]
          this.disableView = false;
          this.disableEdit = true;
          this.textTitle = "Ubah Employee"
          this.isShow = true;
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
      }
    },
    onDelete: function (id) {
      deleteData(id, this.delData)
    },
    delData: function (id) {
      try {
        this.isLoading = true
        DELETE(`employee/${id}`).then(() => {
          success("Data Berhasil Dihapus")
          this.loadData()
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onView: function (id) {
      try {
        this.isLoading = true;
        POST('employee/employee_cr', { Id: id }).then((res) => {
          this.form = res[0]
          this.disableView = true;
          this.isShow = true;
          this.textTitle = "Lihat Employee"
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
      }
    },
    resetForm: function () {
      this.form = {
        Id: null,
        Name: null,
        OfficePhone: null,
        HandPhone: null,
        Email: null,
        Divisi: null,
        JenisLayanan: null,
        PICExperd: null,
        ClientCompanyID: null,
      }
    }
  },
  watch: {
    pagination: {
      handler() {
        try {
          this.isLoading = true;
          const { page, rowsPerPage } = this.pagination;
          POST(`employee/employee_cr/${page}/${rowsPerPage}`).then(res => {
            this.dataEmployee = res.Data.map(x => {
              return {
                Id: x.Id,
                Name: x.Name,
                OfficePhone: x.OfficePhone,
                HandPhone: x.HandPhone,
                Email: x.Email,
                JenisLayanan: x.JenisLayanan,
                PICExperd: x.PICExperd,
                ClientCompanyID: x.ClientCompanyID,
              };
            });
            this.isLoading = false;
            this.totalData = res.TotalData;
          });
        } catch (err) {
          this.isLoading = false;
          error("Error");
        }
      },
      deep: true
    }
  }
};
